export default {
    namespaced: true,
    state: {
        scholarTests: []
    },
    mutations: {
        SET_SCHOLAR_TESTS: (state, data) => {
            state.scholarTests=data
        },
        PASS_SCHOLAR_TEST: (state, data) => {
            state.scholarTests=state.scholarTests.filter(ele=>ele.id!=data.test.id)
        },
        SET_AVAILABLE_SCHOLAR_TESTS: (state, data) => {
            state.scholarTests=data
        }
    },
    actions: {
        async fetchScholarTests({ commit }, id) {
            try {
                const response = await this.$axios.get(`/scholar/${id}/tests`)
                commit("SET_SCHOLAR_TESTS",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        },
        async passTest({ commit }, data) {
            try {
                const response = await this.$axios.post(`/scholar/${data.scholar.id}/passtest`, data)
                commit("PASS_SCHOLAR_TEST",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        },
        async fetchAvailableScholarTests({ commit }, id) {
            try {
                const response = await this.$axios.get(`/scholar/${id}/availabletests`)
                commit("SET_AVAILABLE_SCHOLAR_TESTS",response.data);
            }
            catch (error) {
                console.log(error.data)
            }
        }
    }
}